<template>
  <div class="order">
    <van-nav-bar title="我的订单" left-arrow @click-left="$router.go(-1)" />
<van-tabs v-model="active" sticky>
  <van-tab name="all" title="全部"></van-tab>
  <van-tab name="payment" title="待支付"></van-tab>
  <van-tab name="delivery" title="待发货"></van-tab>
  <van-tab name="received" title="待收货"></van-tab>
  <van-tab name="comment" title="待评价"></van-tab>
</van-tabs>
    <OrderListItem v-for="item in list" :key="item.order_id" :item="item"></OrderListItem>
  </div>
</template>

<script>
import { getMyOrderList } from '@/api/order'
import OrderListItem from '@/components/OrderListItem.vue'
export default {
  name: 'OrderPage',
  components: {
    OrderListItem
  },
  data () {
    return {
      active: this.$route.query.dataType || 'all', // !有三处作用 1.通过地址栏传的参数来active高亮 tab标签 同时接受一波这个dataType参数(user.vue传过来的),接收不到就直接all
      page: 1,
      list: []
    }
  },
  methods: {
    // 获取全部订单/待支付/待发货/待收货/ 的列表
    async getOrderList () {
      console.log(this.active)
      //! 2.因为van会让active等于 被点击到的tab 的name,所以这里同时也把all/payment/delivery/received/comment 动态的传进this.active了 => 动态的变化页面
      //! 3. active: this.$route.query.dataType,这代表着user.vue传过来的参数同样会改变active, 改变active => 变化页面
      const { data: { list } } = await getMyOrderList(this.active, this.page)
      list.data.forEach((item) => {
        item.total_num = 0
        item.goods.forEach(goods => {
          item.total_num += goods.total_num
        })
      })
      // 获取全部订单/待支付/待发货/待收货/ 的列表这个才是
      this.list = list.data
      // console.log(JSON.parse(JSON.stringify(this.list)))
      // console.log(this.list)
    }
  },
  // 一旦active(地址栏参数)一发生改变,则立刻让它重新获取
  watch: {
    active: {
      immediate: true,
      handler () {
        // 重新获取 全部订单/待支付/待发货/待收货/ 的列表
        this.getOrderList()
      }
    }
  }
}
</script>

<style lang="less" scoped>
.order {
  background-color: #fafafa;
}
.van-tabs {
  position: sticky;
  top: 0;
}
</style>
