import request from '@/utils/request'

// 订单结算页获取
/*
mode       string 必需 结算模式（buyNow立即购买 cart购物车） 示例值: buyNow
delivery   integer 必需 配送方式（10快递配送 20上门自提） 示例值: 10
shopId     integer  自提门店ID  必需  示例值:  0
couponId   integer  优惠券ID  必需  示例值:  0
isUsePoints integer 可选 是否使用积分抵扣（1使用 0不使用） 示例值: 0
模式cart     cartIds    string  购物车ID集  可选  示例值:  30,31
模式buyNow  "goodsId": "10038",
模式buyNow  "goodsNum": "1",
模式buyNow  "goodsSkuId": "0"
*/
// mode: cart    => obj { cartIds }
// mode: buyNow  => obj { goodsId  goodsNum  goodsSkuId }
export const checkOrder = (mode, obj) => {
  return request.get('/checkout/order', {
    params: {
      mode, // cart buyNow
      delivery: 10,
      shopId: 0,
      couponId: 0,
      isUsePoints: 0,
      ...obj// 将传递过来的参数对象 动态展开,这样就能自定义传进来的obj了
    }
  })
}

// 提交订单
// mode: cart    => obj { cartIds, remark }
// mode: buyNow  => obj { goodsId, goodsNum, goodsSkuId, remark }
export const sumitOrder = (mode, obj) => {
  return request.post('/checkout/submit', {
    mode, // cart buyNow
    delivery: 10,
    shopId: 0,
    couponId: 0,
    isUsePoints: 0,
    payType: 10,
    ...obj// 将传递过来的参数对象 动态展开,这样就能自定义传进来的obj了
  })
}
// 订单列表
export const getMyOrderList = (dataType, page) => {
  return request.get('/order/list', {
    params: {
      dataType,
      page // List中的分页
    }
  })
}
// 确认收货
export const comfirmReceipt = (orderId) => {
  return request.post('/order/receipt', {
    orderId
  })
}
// 取消订单
export const cancelProduct = (orderId) => {
  return request.post('/order/cancel', {
    orderId
  })
}
