<template>
  <div class="search">
    <van-nav-bar fixed title="商品列表" left-arrow @click-left="$router.go(-1)" />

    <van-search
      readonly
      shape="round"
      background="#ffffff"
      :value="search || '搜索商品'"
      show-action
      @click="$router.push('/search')"
    >
      <template #action>
        <van-icon class="tool" name="apps-o" />
      </template>
    </van-search>

    <!-- 排序选项按钮 -->
    <van-tabs @click="filter">
  <van-tab name="zonghe" title="综合"></van-tab>
  <van-tab name="Sales" title="销量"></van-tab>
  <van-tab  name="price" title="价格" >
  <template #title>
    <van-icon name="arrow-down"  v-show="sortPrice"/><van-icon name="arrow-up" v-show="!sortPrice" />
    价格
  </template>
</van-tab>

  </van-tabs>

    <div class="goods-list">
      <!-- 渲染搜到的值 -->
      <GoodsItem v-for="item in proList" :key="item.goods_id" :item="item"></GoodsItem>
      <van-empty  v-if="isNone"  image="search" description="暂未搜到" />
    </div>
  </div>
</template>

<script>
import GoodsItem from '@/components/GoodsItem.vue'
import { getProList } from '@/api/product'
export default {
  name: 'SearchIndex',
  components: {
    GoodsItem
  },
  data () {
    return {
      search: '',
      proList: [], // 搜索到的商品列表
      isNone: false, // 搜不到商品则为true,默认为false
      page: 1,
      sortPrice: 0, // 0-价格从低到高， 1-价格从高到低
      active: 0, // 标签页高亮下标
      categoryId: ''// 分类商品的分类id
    }
  },
  async created () {
    // 点击分类页的分类商品的分类id
    this.categoryId = this.$route.query.categoryId
    // 搜索框的搜索内容保留并回显
    this.search = this.$route.query.search
    // 将搜索词传给api并接收api传过来的值
    const obj = { categoryId: this.categoryId, goodsName: this.search, page: this.page, sortPrice: this.sortPrice }
    const res = await getProList(obj)
    // console.log(res)
    this.proList = res.data.list.data
    // console.log(this.proList)
    // 如果搜不到则让isNone为真,则v-if(暂未搜到的图案)生效
    if (res.data.list.total === 0) {
      this.isNone = true
    }
    // console.log(this.categoryId)
  },
  methods: {
    async filter (name, title) {
      if (name === 'price') {
        this.sortPrice = this.sortPrice === 1 ? 0 : 1
        const obj = { goodsName: this.search, page: this.page, sortPrice: this.sortPrice, sortType: 'price' }
        const res = await getProList(obj)
        this.proList = res.data.list.data
      }
      if (name === 'Sales') {
        const obj = { goodsName: this.search, page: this.page, sortPrice: this.sortPrice, sortType: 'sales' }
        const res = await getProList(obj)
        this.proList = res.data.list.data
      }
      if (name === 'zonghe') {
        const obj = { goodsName: this.search, page: this.page, sortPrice: this.sortPrice, sortType: 'all' }
        const res = await getProList(obj)
        this.proList = res.data.list.data
      }
    }
  }
}
</script>

<style lang="less" scoped>
.search {
  padding-top: 46px;
  ::v-deep .van-icon-arrow-left {
    color: #333;
  }
  .tool {
    font-size: 24px;
    height: 40px;
    line-height: 40px;
  }

  .sort-btns {
    display: flex;
    height: 36px;
    line-height: 36px;
    .sort-item {
      text-align: center;
      flex: 1;
      font-size: 16px;
    }
  }
}

// 商品样式
.goods-list {
  background-color: #f6f6f6;
}
.van-empty{
  height: 550px;
}
</style>
